import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import '../scss/Detail.scss';

let 박스 = styled.div`
    padding: 20px;
`;
let 제목 = styled.h4`    
    font-size : 25px;
    color: ${props => props.색상};
`;

class Detail2 extends React.Component{
    componentDidMount(){
        //컴포넌트가 Mount 되었을때 실행할 코드 (렌더링 될때)
        //주로 Ajax 같은 것도 이런 곳에서 자주 사용
    }
    componentWillUnmount(){
        //컴포넌트가 Unmount 되었을때 실행할 코드 (사라질 때)
    }
}



function DetailPage(props){
    let [is_alert,is_alert_fn] = useState(true);
    let [inputData, inputData_fn] = useState('');

    useEffect(()=>{
        //컴포넌트가 mount 되었을 때
        //컴포넌트가 update 될 때        
        let timer = setTimeout(()=>{            
            // const alert_box =  document.getElementsByClassName('.my-alert my-alert2');
            // const alert_box =  document.querySelector('.my-alert my-alert2');
            //alert_box.setAttribute('style','display:none');
            // alert_box.style.display='none';            
            is_alert_fn(false);
        },2000);
        // return function closerAction(){ alert('unmount') }
        return ()=>{ console.log('closer action'); clearTimeout(timer);  }
    },[]);

    let {id} = useParams();
    let history = useHistory();
    const shoes_detail = props.shoes.find((item)=>{ return item.id==id; });
    const shoes_url = `https://codingapple1.github.io/shop/shoes${(parseInt(id)+1)}.jpg`;

    const 재고 = props.재고;

    return(
        <div>
        <div className="container">
            <박스>
                <제목 색상={'tomato'} >Product</제목>
                <제목 className="red" >Detail</제목>
            </박스>

            {inputData}
            <input onChange={(e)=>{
                inputData_fn(e.target.value);
            }} />

            {
                is_alert
                ?
                <Myalet />:null
            }
            <div className="row">
            <div className="col-md-6">
                <img src={shoes_url} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
                <h4 className="pt-5">{shoes_detail.title}</h4>
                <p>{shoes_detail.content}</p>
                <p>{shoes_detail.price}</p>

                <Info 재고={재고[id]} />

                <button className="btn btn-danger" onClick={()=>{
                    let 사본재고 = [...재고];
                    // console.log(사본재고);
                    사본재고[id] = 사본재고[id]-1;
                    // console.log(사본재고[0]);
                    // props.재고변경([9,10,11]);
                    props.재고변경(사본재고);
                }}
                >주문하기</button>
                &nbsp;
                <button className="btn btn-danger" onClick={()=>{ 
                    history.goBack();
                    // history.push('/');
                }}>뒤로가기</button>
            </div>
            </div>
        </div>
        </div>
    )
}


function Info(props){
    return (
        <p>재고 : {props.재고}</p>
    )
}


function Myalet(){
    return(
        <div className="my-alert my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
        </div>
    )
}

export default DetailPage;